@font-face {
  font-family: 'SEGA LOGO FONT';
  font-style: normal;
  font-weight: normal;
  src: local('SEGA LOGO FONT'), url(/Robo_Friends_App/static/media/SEGA.12a3db8b.woff) format('woff');
}


h1 {
  font-family: 'SEGA LOGO FONT';
  font-weight: 200;
  color: aqua;
}

/* must type to works  */
body {
  margin: 0;
  padding: 0;
  background: linear-gradient(to right, rgb(50, 69, 61), rgb(30, 90, 143));
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
